body{
  padding: 0;
  box-sizing: border-box;
}
.App {
  text-align: center;
}
p{
  color: white;
}
.Form{
  /* This bit sets up the horizontal layout */
  display:flex;
  flex-direction:row;
  margin: 1rem auto 3rem;
  /* This bit draws the box around it */
  /* border:1px solid grey; */
  border-bottom: 0.3rem solid #E50914;
  border-radius: 0.5rem;
  background-color: white;
  /* I've used padding so you can see the edges of the elements. */
  padding:2px;
  width: 30rem;
  height: 2rem;
}
.Form input[type=text]{
  flex-grow: 2;
  border: none;
  /* border: 0.25rem solid #E50914;
  border-radius: 1rem;
  width: 30rem;
  height: 2rem;
  margin-bottom: 4rem; */
}
.Form input[type=text]:focus {
  /* removing the input focus blue box. Put this on the form if you like. */
  outline: none;
}
.submit-btn{
  /* border: 0.25rem solid #E50914;
  border-radius: 1rem;
  width: 4rem;
  height: 2rem;
  margin-bottom: 4rem; */
  color: red;
  opacity: 0.7;
  background-color: white;
  flex-grow: 0;
  border: none;
  margin-right: 10px;
  outline:none;
}
.submit-btn:hover{
  cursor: pointer;
}
.Movietemplate img{
  width: 16rem;
  height: 22.5rem;
  border-radius: 1rem;
}
.Movietemplate img:hover{
  cursor: pointer;
}
.Movietemplate h1{
  font-family: 'Bebas Neue', cursive;
  width: 15rem;
  margin-bottom: 3rem;
}
.Movietemplate h1:hover{
  cursor: pointer;
}
.not-searched-movie{
  display: none;
}
