.Recommendation{
  margin-top: 3rem;
  color: white;
  display: flex;
  justify-content: space-around;
  flex-wrap: wrap;
}



@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}
body{
  background-image: url("https://raw.githubusercontent.com/kishan0725/AJAX-Movie-Recommendation-System-with-Sentiment-Analysis/master/static/image.jpg");
  /* background-repeat: no-repeat; */
  /* background-color: rgba(225, 255, 255, 0.2);
  background-blend-mode: lighten; */
}
.logo{
  color: #E50914;
  padding: 2rem ;
  font-family: 'Bebas Neue', cursive;
  text-align: center;
  font-size: 8rem;
  padding: 0;
  padding-top: 2rem;
}
.inst{
  color: white;
  text-align: center;
  font-family: 'Montserrat', sans-serif;
  font-size: 2.5rem;
  margin: 0 auto 3rem;
  
}
.inst p{
  display: inline;
  margin-top: 0px;
  border-bottom: 0.3rem solid #E50914;
  border-radius: 0.5rem;
}


.modalcontent{
  position: absolute;
  top: 40px ;
  left: 40px;
  right: 40px;
  bottom: 40px;
  border: 1px solid #ccc;
  overflow: auto;
  -webkit-overflow-scrolling: touch;
  border-radius: 1rem;
  outline: none;
  margin: 0 10%;
  padding: 20px;
  color: white;
  background-color: #232323;
  display: flex;
  justify-content: space-between;

}
.searched-movie-details{
  margin: 0 10%;
  padding: 0 20px 20px;
  color: white;
  display: flex;
  justify-content: space-between;
}
.left img{
  width: 20rem;
  height: 30rem;
  border-radius: 1rem;
}
.left h1{
  text-align: center;
  font-family: 'Bebas Neue', cursive;
}
.left{
  padding: 0 10rem;
}
.right{
  display: flex;
  flex-direction: column ;
  justify-content: center;
  margin-right: 4rem;
  padding-right: 2rem;
  /* padding-top: 4rem; */
}
.right .info-heading{
  font-weight: bold;
}
.right p{
  text-align: left;
  font-size: 1.25rem;
}
.right .homepage-link{
  text-align: left;
  color: aqua;
  font-size: 1.25rem;
  font-family: 'Montserrat', sans-serif;
  font-weight: bold;
  text-decoration: none;
}
.close-modal{
  position: absolute;
  top: 0.5rem;
  right: 0.5rem;
}
.close-modal button{
  outline: none;
  border: none;
  color: white;
  background-color: #232323;
}
.close-modal button:hover{
  cursor: pointer;
}
.invalid_search{
  color: white;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
